import { Component, OnInit, Inject, Injector, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';


import { PurchaseOrderService } from 'src/app/api/controllers/PurchaseOrder';
import { StaffService } from 'src/app/api/controllers/Staff';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ModalService } from 'src/app/shared/services/modal.service';


@Component({
  selector: 'app-inventory-setup-warehouse',
  templateUrl: './inventory-setup-warehouse.component.html',
  styleUrls: ['./inventory-setup-warehouse.component.scss'],
	encapsulation: ViewEncapsulation.None,
})
export class InventorySetupWarehouseComponent implements OnInit {


  	isDataLoaded:boolean = false;
  	isError:boolean = false;

	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;

	searchWarehouse:any;

	unitFieldType: string = "unitno";
	unitOrder: boolean = false;

	row: any = {};
	locationList: any;
	staffList: any;

	message: any;

	warehouseData: any = [];
	tempWarehouseData: any = []
	wareHouseIndex: any;
	backupWarhouse:any = {};
  

  	modalService: any;

  constructor(
    public dialog: MatDialog,
    private injector: Injector,
    private purchaseOrderService: PurchaseOrderService,
    private staffService: StaffService,
    private sharedService: SharedService,
    private lookupService: LookupService,
    private sessionService: SessionService
  ) {

    this.modalService = this.injector.get(ModalService);

   }

  isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

  sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

  getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

  filterWareHouse(value) {
    this.warehouseData = this.tempWarehouseData.filter((item: any) =>{
      item.warehouseName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1;
    })
  }

  getAllStaff() {
		let staffParms = {
			apartmentId: this.sessionService.apartmentId,
			roleTypeIds: this.sessionService.roleTypeId
		}
		this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
			if(res.length) {
				this.staffList = res;
			}
		},error => {
			console.log(error)
		});
	}

  getAllGetAllWarehouse() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.sessionService.apartmentId

		};
		this.isDataLoaded = false;
		this.purchaseOrderService.getAllWarehouseByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.warehouseData = res && res.length > 0 ? res.reverse() : [];
				this.isDataLoaded = true;
				this.tempWarehouseData = this.warehouseData;
				this.totalItems = this.warehouseData.length;
				if (this.warehouseData && this.warehouseData.length > 0) {
					this.warehouseData.filter(val => {
						if (val.warehouseName == '') {
							delete val.warehouseName;
							delete val.warehouseId;
						}

					});

				}


				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}


			}
		});
	}

  addWareHouse(data) {

		
    	let reqObj = {
			"warehouseId": data.warehouseId,
			"apartmentId": this.sessionService.apartmentId,
			"warehouseName": data.warehouseName,
			"towerLocation": data.towerLocation,
			"unitOfMeasureId": null,
			"descripition": data.descripition,
			"owner": data.owner,
			"supervisor": data.supervisor,
			"isActive": true,
			"insertedBy": this.sessionService.userId,
			"insertedOn": new Date().toISOString,
			"updatedBy": null,
			"updatedOn": null,
		}
		
		let params:any = {
			warehouseobj: reqObj
		}

		this.purchaseOrderService.addWarehouse(params).subscribe((res: any) => {
			if (res.message) {
				this.sharedService.openSnackBar('Warehouse added successfully','success');
				this.isDataLoaded = true;
				this.row = {};
				this.getAllGetAllWarehouse();
			}

		});
	}

	updateWareHouse(data) {
		
		
    let reqObj = {
			"warehouseId": data.warehouseId,
			"apartmentId": this.sessionService.apartmentId,
			"warehouseName": data.warehouseName,
			"towerLocation": data.towerLocation,
			"unitOfMeasureId": null,
			"descripition": data.descripition,
			"owner": data.owner,
			"supervisor": data.supervisor,
			"isActive": true,
			"insertedBy": data.insertedBy,
			"insertedOn": data.insertedOn,
			"updatedBy": this.sessionService.userId,
			"updatedOn": new Date().toISOString,
		}

		let params:any = {
			warehouseobj: reqObj
		}
		this.purchaseOrderService.updateWarehouse(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.openSnackBar("Warehouse updated successfully", 'success');
				this.isDataLoaded = true;
				this.wareHouseIndex = -1;
			}

		});
	}

  	setCurrentData(data) {
		this.backupWarhouse  = {...data}
	}

	cancelWarHouse() {
		let index = this.warehouseData.findIndex((data)=>data.warehouseId == this.backupWarhouse.warehouseId);
		this.warehouseData.splice(index,1,this.backupWarhouse);
	}

  	deleteRow(data) {
		this.modalService.showConfirmModal(data.warehouseId);
	}

  

	ngOnInit(): void {

		this.getAllGetAllWarehouse();
		this.getAllStaff();

		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			
			if (id != null) {

				let param = {
					apartmentId: this.sessionService.apartmentId,
					warehouseId: id,
					deleteBy: parseInt(this.sessionService.userId)
				}

				this.purchaseOrderService.deleteWarehouse(param).subscribe((res: any) => {
					this.getAllGetAllWarehouse();
					setTimeout(() => {
						this.sharedService.openSnackBar("Warehouse deleted successfully", 'success');
						this.sharedService.setUnitListDeleteIndex(null);
					}, 500)
				}, error => {
					console.log(error);
				});
			}
		});


	}

}
